<template>
    <div class="last">
        <mt-navbar>
            <mt-tab-item v-for="primelist in primelists" :key="primelist.id" :sel="selected">
                <img  :src="primelist.url" class="img1" slot="icon" @click="topages(primelist)">
                <!--<img v-show="bol" :src="primelist.url1" class="img1" slot="icon" @click="topages(primelist)">-->
                <span style="font-size:14px;text-align :center ;" >{{primelist.title}}</span>
            </mt-tab-item>
            <!--<mt-tab-item style="width:25%">-->
            <!--<img :src="shared1" class="img1" slot="icon" @click="topages1">-->
            <!--<span style="font-size:14px;text-align :center ;" >首页</span>-->
            <!--</mt-tab-item>-->
            <!--<mt-tab-item style="width:25%">-->
            <!--<img :src="shared2" class="img1" slot="icon" @click="topages2">-->
            <!--<span style="font-size:14px;text-align :center ;" >发现</span>-->
            <!--</mt-tab-item>-->
            <!--<mt-tab-item style="width:25%">-->
            <!--<img :src="shared3" class="img1" slot="icon" @click="topages3">-->
            <!--<span style="font-size:14px;text-align :center ;" >消息</span>-->
            <!--</mt-tab-item>-->
            <!--<mt-tab-item style="width:25%">-->
            <!--<img :src="shared4" class="img1" slot="icon" @click="topages4">-->
            <!--<span style="font-size:14px;text-align :center ;" >我的</span>-->
            <!--</mt-tab-item>-->
        </mt-navbar>
    </div>
</template>

<script>
    import shared_nor1 from '../../static/home-false.png'
    import shared_nor2 from '../../static/faxian-false.png'
    import shared_nor3 from '../../static/xiaoxi-false.png'
    import shared_nor4 from '../../static/wode-false.png'
    import shared_hover1 from '../../static/home-true.png'
    import shared_hover2 from '../../static/faxian-true.png'
    import shared_hover3 from '../../static/xiaoxi-true.png'
    import shared_hover4 from '../../static/wode-true.png'

    import { Navbar, TabItem,Button,Field,Header,Swipe, SwipeItem,TabContainer, TabContainerItem,Cell } from 'mint-ui';
    export default {
        name: "home",
        components: {
            [Navbar.name]:Navbar,
            [TabItem.name]:TabItem,
            [Button.name]:Button,
            [Field.name]:Field,
            [Header.name]:Header,
            [Swipe.name]:Swipe,
            [SwipeItem.name]:SwipeItem,
            [TabContainer.name]:TabContainer,
            [TabContainerItem.name]:TabContainerItem,
            [Cell.name]:Cell
        },

        data () {
            return {
                selected:'home',
                shared1:'',
                shared2:'',
                shared3:'',
                shared4:'',
                showicon:false,
                active: 'tab-container1',
                primelists:[
                    {
                        id:'1',
                        name:'home',
                        title:'首页',
                        url:'../../static/home-false.png',
                        url1:"../../static/home-true.png"
                    },
                    {
                        id:'2',
                        name:'discovery',
                        title:'发现',
                        url:'../../static/faxian-false.png',
                        url1:'../../static/faxian-true.png'
                    },
                    {
                        id:'3',
                        name:'message',
                        title:'消息',
                        url:'../../static/xiaoxi-false.png',
                        url1:'../../static/xiaoxi-true.png'
                    },
                    {
                        id:'4',
                        name:'mine',
                        title:'我的',
                        url:'../../static/wode-false.png',
                        url1:'../../static/wode-true.png'
                    }
                ]
            }
        },
        computed:{
            bol: function(){
                console.log(sel);
                if(this.sel == this.page){
                    return true;
                }
                return false;
            }
        },
        methods:{
            topages:function(primelist){

                if(primelist.id==1) {
                    // this.shared=primelist.url1;
                    this.selected = primelist.name;
                    this.$router.push({path: '/home'})
                }
                else if(primelist.id==2){
                    //this.shared=primelist.url1;
                    this.selected = primelist.name;
                    this.$router.push({path: '/discovery'})
                }
                else if(primelist.id==3){
                    //this.shared=primelist.url1;
                    this.selected = primelist.name;
                    this.$router.push({path: '/messages'})
                }
                else if(primelist.id==4){
                    //this.shared=primelist.url1;
                    this.selected = primelist.name;
                    this.$router.push({path: '/mine'})
                }
            },
            // topages1(){
            //
            //     this.shared1=shared_hover1;
            //     this.$router.push({path: '/home'})
            // },
            // topages2(){
            //     this.$router.push({path: '/discovery'})
            // },
            // topages3(){
            //     this.$router.push({path: '/messages'})
            // },
            // topages4(){
            //     this.$router.push({path: '/mine'})
            // },
            // change(item,index) {
            //     this.$router.push(item.push);
            //     this.$emit("change",index)
            // }

        },
        // mounted(){
        //     //console.log(primelist);
        //     this.shared1=shared_nor1;
        //     this.shared2=shared_nor2;
        //     this.shared3=shared_nor3;
        //     this.shared4=shared_nor4;
        // }
    }
</script>

<style scoped>
    .last{
        position:fixed;
        bottom: 0;
        right:0;
        left:0;
    }
    .img2{
        display: none;
    }
    .on .img1{
        display: none;
    }
    .on .img2{
        display: block;
    }
</style>



